p, h3, article, section {
  margin: 0;
}

.page {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__nav {
  margin-right: 50px;
}

.header__logo {
  margin-left: 50px;
  display: flex;
}

.header__logo-image {
  width: 40px;
  height: 40px;
}

.nav {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  list-style: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.is-active {
  color: #00acdc;
}

* {
  box-sizing: border-box;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  transition-duration: .3s;
}

.card__content {
  flex-direction: column;
  justify-content: center;
  padding: 32px 16px 16px;
  display: flex;
}

.card__photo {
  object-fit: contain;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__buy {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.content {
  grid-gap: 46px 48px;
  grid-template-rows: auto;
  grid-template-columns: 200px;
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .content {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .content {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .content {
    grid-template-columns: repeat(4, 200px);
  }
}

